* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3 {
  color: rgb(71, 62, 62);
}
body {
  font-family: "Mali", cursive;
}
h3,
h4 {
  font-weight: 400;
}
.App {
  transition: all 0.5s ease;
}
.library-active {
  margin-left: 30%;
}

@import "./song";
@import "./player";
@import "./library";
@import "./Nav";
